import React from 'react'
import { navigate } from 'gatsby'
import {
  Header,
  Form,
  Input,
  Button,
  Segment,
  Message,
} from 'semantic-ui-react'
import Helmet from 'react-helmet'
import AuthContext from '../components/Context/AuthContext'
import Layout from '../components/Layout/layout'

import { register } from '../../lib/moltin'

export default class Register extends React.Component {
  state = {
    name: '',
    email: '',
    password: '',
    loading: false,
    errors: null,
  }

  _handleSubmit = (e, context) => {
    e.preventDefault()

    const { name, email, password } = this.state

    this.setState({
      loading: true,
    })
    register({ name, email, password })
      .then(data => {
        const { id, token } = data
        localStorage.setItem('customerToken', token)
        localStorage.setItem('mcustomer', id)
        context.updateToken()
        navigate('/myaccount/')
      })
      .catch(e => {
        console.log(e)
        this.setState({
          loading: false,
          errors: e.errors || e,
        })
      })
  }

  _handleChange = ({ target: { name, value } }) =>
    this.setState({ [name]: value })

  handleErrors = errors => {
    if (!Array.isArray(errors) && !errors.length > 0) {
      return (
        <Message error header="Sorry" content="Cannot register at this time." />
      )
    }
    return errors.map((error, i) => (
      <Message error header={error.title} content={error.detail} key={i} />
    ))
  }

  render() {
    const { loading, errors } = this.state

    return (
      <Layout location={this.props.location}>
        <AuthContext.Consumer>
          {context => (
            <React.Fragment>
              <Helmet title="Register" />
              <Header as="h1">Créer un compte</Header>

              <Form
                onSubmit={e => this._handleSubmit(e, context)}
                loading={loading}
                error={!!errors}
              >
                {errors ? this.handleErrors(errors) : null}
                <Segment>
                  <Form.Field>
                    <label htmlFor="name">Nom</label>
                    <Input
                      id="name"
                      fluid
                      name="name"
                      autoFocus
                      required
                      onChange={e => this._handleChange(e)}
                    />
                  </Form.Field>

                  <Form.Field>
                    <label htmlFor="email">Adresse Email</label>
                    <Input
                      id="email"
                      fluid
                      name="email"
                      type="email"
                      required
                      onChange={e => this._handleChange(e)}
                    />
                  </Form.Field>

                  <Form.Field>
                    <label htmlFor="password">Mot de passe</label>
                    <Input
                      id="password"
                      fluid
                      name="password"
                      type="password"
                      required
                      onChange={e => this._handleChange(e)}
                    />
                  </Form.Field>

                  <Button type="submit" color="black">
                    S'enregistrer
                  </Button>
                </Segment>
              </Form>
            </React.Fragment>
          )}
        </AuthContext.Consumer>
      </Layout>
    )
  }
}
